.btn-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn {
  width: 100px;
  height: 40px;
  text-transform: uppercase;
  color: #f1fff9;
  background-color: rgba(98, 93, 245, .565);
  border-radius: 4px;
  box-shadow: 5px 15px 30px 1px rgba(0, 0, 0, .28);
}

.btn[disabled] {
  background-color: rgba(98, 93, 245, .25);
}

